import { useState } from 'react'
import OSWindow from '../oswindow'

interface ScoreProps {
	id: string
	isActive: boolean
	setActive: (id: string) => void
	score: number
}

const Score = ({ id, isActive, setActive, score }: ScoreProps) => {
	return (
		<OSWindow
			id={id}
			title='SCORE CARD'
			size='small'
			startingShift={0}
			setActive={setActive}
			isActive={isActive}
		>
			<span>{score}</span>
		</OSWindow>
	)
}

export default Score
